import React, {useState, useEffect} from 'react';
import Panel from '@vkontakte/vkui/dist/components/Panel/Panel';
import PanelHeader from '@vkontakte/vkui/dist/components/PanelHeader/PanelHeader';
import FormStatus from '@vkontakte/vkui/dist/components/FormStatus/FormStatus';
import Input from '@vkontakte/vkui/dist/components/Input/Input';
import FormLayout from '@vkontakte/vkui/dist/components/FormLayout/FormLayout';
import Button from "@vkontakte/vkui/dist/components/Button/Button";
import SelectMimicry from "@vkontakte/vkui/dist/components/SelectMimicry/SelectMimicry";
import Root from "@vkontakte/vkui/dist/components/Root/Root";
import View from "@vkontakte/vkui/dist/components/View/View";
import Group from "@vkontakte/vkui/dist/components/Group/Group";
import Cell from "@vkontakte/vkui/dist/components/Cell/Cell";
import List from "@vkontakte/vkui/dist/components/List/List";
import Icon24Done from "@vkontakte/icons/dist/24/done";
import Div from "@vkontakte/vkui/dist/components/Div/Div";
import PanelHeaderBack from "@vkontakte/vkui/dist/components/PanelHeaderBack/PanelHeaderBack";


const Vkpay = ({id, go}) => {

    return (


        <Panel id={id} theme="white">
            <PanelHeader left={<PanelHeaderBack onClick={go} data-to="payment system"/>}
            >Оплата
            </PanelHeader>
            <FormLayout>
                <SelectMimicry
                    top="Выберите способ оплаты"
                    placeholder="Не выбран"
                    onClick={go} data-to="payment system"
                >VK Pay
                </SelectMimicry>
            </FormLayout>
            <Div>
                <Div>Оплата через VK Pay</Div>
                <FormLayout>
                    <Input top="Мобильный телефон" defaultValue="+7"/>
                    <Input top="Код подтверждения"/>
                    <Button size="xl" onClick={go} data-to="payment">
                        Оплатить
                    </Button>
                </FormLayout>
            </Div>
        </Panel>
    );
};

export default Vkpay;
